const axios = require("axios");
function request(url) {
  return axios({
    url,
    method: "GET",
    headers: {
      accept: "application/json"
    }
  });
}

module.exports = () => {
  const div = document.createElement("div");
  const bands = document.createElement("ul");
  const songs = document.createElement("ul");
  div.innerHTML = "I am trying";

  document.body.appendChild(div);

  request("https://api.mocki.io/v1/1767b67d").then(({ data }) => {
    for (const item of data.results) {
      const li = document.createElement("li");
      li.innerHTML = `Band: ${item.band} Genre ${item.genre}`;
      bands.append(li);
    }
    div.append(bands);
  });

  request("https://api.mocki.io/v1/1db31e8e").then(({ data }) => {
    for (const item of data.results) {
      const li = document.createElement("li");
      li.innerHTML = `Song: ${item.song} Band ${item.band}`;
      songs.append(li);
    }
    div.append(songs);
  });
};
